<template>
  <body class="d-flex flex-column">
    <!-- <div>Form: {{ voucher_type }}</div> -->

    <v-dialog v-model="dialog" persistent width="300">
      <v-card color="primary" dark class="px-4 pt-2">
        <v-card-text>
          Loading ...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-form class="px-3" style="max-width: 500px !important">
      <h2 class="my-4">Get Voucher</h2>
      <v-container width="100%" fluid>
        <v-row width="100%" class="d-flex justify-center">
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-text-field
              label="Name"
              v-model="input.name"
              :rules="textInputRules"
              outlined
              required
              width="100%"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-text-field
              label="Email"
              v-model="input.email"
              :rules="emailRules"
              outlined
              required
              width="100%"
              hint="**pastikan alamat email anda benar karena voucher akan dikirimkan lewat email."
              persistent-hint
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0 mt-3">
            <v-text-field
              label="Phone"
              v-model="input.phone"
              :rules="phoneNumberRules"
              outlined
              required
              width="100%"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row width="100%">
          <v-col
            width="100%"
            class="d-flex justify-start align-center pa-0 mt-16 mb-8"
          >
            <v-checkbox
              v-model="checkbox"
              color="success"
              hide-details
            ></v-checkbox>
            <div style="font-size: 12px !important; letter-spacing: 0.6px">
              I agree to receive communications via Email/ Call/ Whatsapp from
              <a target="_blank" href="https://gli.id/">
                PT. Global Loyalty Indonesia
              </a>
              (Alfa Group) about this programme and other relevant programmes.
            </div>
          </v-col>
        </v-row>

        <v-row width="100%" class="d-flex justify-start">
          <v-btn
            class="white--text"
            width="100%"
            height="45px"
            block
            color="#f77a4a"
            @click="onSubmit()"
            >Get The Voucher</v-btn
          >
        </v-row>
      </v-container>
    </v-form>
  </body>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import { api_url } from "../global_variable/global_variable.js";
import sha256 from "js-sha256";

export default {
  name: "Form",

  components: {},

  data() {
    return {
      dialog: false,
      pe_id: null,
      voucher_type: null,
      pe_end_date: null,
      subject_mail: null,
      checkbox: false,
      input: {
        name: null,
        email: null,
        phone: "08",
      },
      textInputRules: [(v) => !!v || "Kolom harus diisi"],
      emailRules: [
        (v) =>
          (!!v && /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v)) ||
          "E-mail tidak valid",
      ],
      phoneNumberRules: [
        (v) =>
          (v.length >= 10 && String(v).substring(0, 1) == "0") ||
          "Nomor hp tidak valid",
      ],
      rules: {},
    };
  },

  created() {},

  async mounted() {
    if (!localStorage.voucher_token) {
      this.$router.push({ name: "notFound" });
    } else {
      localStorage.removeItem("date_end");
      localStorage.removeItem("voucher_token");
      this.pe_id = localStorage.pe_id;
      this.voucher_type = localStorage.voucher_type;
      this.pe_end_date = localStorage.pe_end_date;
      this.subject_mail = localStorage.subject_mail;
      this.client_ip = localStorage.client_ip;
      localStorage.removeItem("pe_id");
      localStorage.removeItem("voucher_type");
      localStorage.removeItem("pe_end_date");
      localStorage.removeItem("subject_mail");
      localStorage.removeItem("client_ip");

      try {
        var d = new Date();
        var m = d.getMonth() + 1;
        var t = d.getDate();
        if (m < 10) {
          m = "0" + m;
        }
        if (t < 10) {
          t = "0" + t;
        }
        var datestring = d.getFullYear() + "" + m + "" + t;
        // console.log(datestring);
        var tgl = datestring;
        var kunci = "SSO" + "4lfag1ft" + tgl + "precise";
        // console.log(kunci);
        var token = sha256(kunci);

        await axios
          .get(
            api_url + "/check-voucher?token=" + token + "&pe_id=" + this.pe_id
          )
          .then((response) => {
            // console.log(response.data);
            if (response.data.response_message != "success") {
              this.$router.push({ name: "voucher-habis" });
              localStorage.removeItem("voucher_token");
              localStorage.removeItem("pe_id");
              localStorage.removeItem("voucher_type");
              localStorage.removeItem("pe_end_date");
              localStorage.removeItem("subject_mail");
              localStorage.removeItem("client_ip");
            }
          })
          .catch((error) => {
            console.log("Form.vue (error) :: ", error);
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }
    }
  },

  methods: {
    async onSubmit() {
      // console.log(this.checkbox);
      if (
        this.checkbox &&
        this.input.name != null &&
        this.input.email != null &&
        this.input.phone != null
      ) {
        this.dialog = true;
        var username =
          this.input.name[0].toUpperCase() + this.input.name.slice(1);
        // console.log(username, this.input.email, this.input.phone);
        // console.log(this.voucher_type);
        try {
          var d = new Date();
          var m = d.getMonth() + 1;
          var t = d.getDate();
          if (m < 10) {
            m = "0" + m;
          }
          if (t < 10) {
            t = "0" + t;
          }
          var datestring = d.getFullYear() + "" + m + "" + t;
          // console.log(datestring);
          var tgl = datestring;
          var kunci =
            "SSO" +
            "4lfag1ft" +
            tgl +
            "precise" +
            this.input.email.toLowerCase();
          // console.log(kunci);
          var token = sha256(kunci);

          var headers = {
            headers: {
              "Content-Type": "application/json",
              Connection: "keep-alive",
            },
          };

          // console.log(this.$route.params.router_data);

          var body = {
            type: this.voucher_type,
            pe_id: this.pe_id,
            name: username,
            email: this.input.email.toLowerCase(),
            phone: this.input.phone,
            body_html: this.$route.params.router_data,
            subject_html: this.subject_mail,
            client_ip: this.client_ip,
          };
          // console.log(body);
          await axios
            .post(api_url + "/assign-voucher?token=" + token, body, headers)
            .then((response) => {
              // console.log(response.data);
              if (response.data.response_message == "success") {
                this.$router.push({ name: "voucher" });
                localStorage.removeItem("voucher_token");
                localStorage.removeItem("pe_id");
                localStorage.removeItem("voucher_type");
                localStorage.removeItem("pe_end_date");
                localStorage.removeItem("subject_mail");
                localStorage.removeItem("client_ip");
              } else if (
                response.data.response_message.includes(
                  "Error : duplicate key value violates unique constraint"
                )
              ) {
                this.$router.push({ name: "email-pernah-digunakan" });
                localStorage.removeItem("voucher_token");
                localStorage.removeItem("pe_id");
                localStorage.removeItem("voucher_type");
                localStorage.removeItem("pe_end_date");
                localStorage.removeItem("subject_mail");
                localStorage.removeItem("client_ip");
              } else {
                alert(
                  "Gagal mengirim kode voucher. Silahkan coba beberapa saat lagi."
                );
              }
            })
            .catch((error) => {
              console.log("Form.vue (error) :: ", error);
            });
        } catch (e) {
          console.error(e);
          alert("Error: koneksi gagal");
        }
      } else {
        alert(
          "Silahkan melengkapi isi form dan menyetujui Terms and Conditions"
        );
      }

      this.dialog = false;
    },
  },
};
</script>

<style scoped>
body {
  /* background-color: #073e5d; */
  background-size: cover;
  /* font-family: Montserrat; */
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
